import cursor from './cursor';
import frame from './frame';
import stylesheet from './style';

export default class Player {
  constructor(store) {
    this.events = store.events;
    this.document = store.document;

    frame.onload = () => {
      frame.contentDocument.write(this.document.innerHTML);
      frame.contentDocument.head.appendChild(stylesheet);
      frame.contentDocument.body.appendChild(cursor);
    };


    const draw = () => {
      if (this.events.length) {
        const currentEvent = this.events.shift();

        cursor.style.top = parseInt(400 * currentEvent.y / store.dimensions.height)+'px';
        cursor.style.left = parseInt(400 * currentEvent.x / store.dimensions.width)+'px';

        requestAnimationFrame(draw);
      }
    };

    draw();

    return this;
  }

  getPlayer() {
    return frame;
  }
}
